import { v4 as uuid } from 'uuid'

import type { DiskWithVmId } from '@oxide/api-mocks'

import { type Disk, type Host, type Storage, type VirtualMachine } from '~/api'
import { json, makeHandlers, type Json } from '~/api/__generated__/msw-handlers'
import { GiB, TiB } from '~/util/units.ts'

import { db, notFoundErr } from './db'
import { NotImplemented, paginated } from './util.ts'

// Note the *JSON types. Those represent actual API request and response bodies,
// the snake-cased objects coming straight from the API before the generated
// client camel-cases the keys and parses date fields. Inside the mock API everything
// is *JSON type.

export const handlers = makeHandlers({
  addStorage({ path, body }) {
    const storage: Json<Storage> = {
      id: uuid(),
      name: body.name,
      mount_point: body.path as string,
      storage_type: body.type,
      host_id: path.hostId,
    }
    db.storages.push(storage)
    const h = db.hosts.find((host) => host.id === path.hostId)
    h?.vm_storages?.push(storage)
    return storage
  },
  getStoragesOnHost({ path, query }) {
    const storages = db.storages.filter((storage) => storage.host_id === path.hostId)
    return paginated(query, storages)
  },
  getVmsOnHost({ path, query }) {
    const vms = db.instances.filter((i) => i.host_id === path.hostId)
    return paginated(query, vms)
  },
  getHost({ path }) {
    const h = db.hosts.find((host) => host.id === path.hostId)
    if (!h) throw notFoundErr(`host '${path.hostId}'`)
    return h
  },
  GetStorages({ query }) {
    const storages = db.storages
    return paginated(query, storages)
  },
  addHost({ body }) {
    const host: Json<Host> = {
      id: uuid(),
      cpu: 16,
      name: body.name,
      vms: 0,
      memory: 256 * GiB,
      storage: 100 * TiB,
      vm_storages: [],
      status: 'READY',
      host_stats: {
        used_cpu: 0,
        used_memory: 0,
        used_storages: 0,
      },
    }

    const storages: Json<Storage>[] = [
      {
        id: uuid(),
        name: '/opt',
        mount_point: '/opt',
        storage_type: 'LOCAL',
        host_id: host.id,
      },
      {
        id: uuid(),
        name: '/data',
        mount_point: '/data',
        storage_type: 'LOCAL',
        host_id: host.id,
      },
    ]
    host.local_storages = storages
    db.hosts.push(host)
    return json({
      host: host,
      job_id: '',
    })
  },
  configVm: NotImplemented,
  createComputeCluster: NotImplemented,
  createDataCenter: NotImplemented,
  createDirectory: NotImplemented,
  createVm({ body }) {
    const id = uuid()
    const vm: Json<VirtualMachine> = {
      id: id,
      name: body.name,
      status: 'CREATING',
      uuid: id,
      cpu: body.cpu,
      memory: body.memory,
      host_id: body.host_id,
      vm_config: '',
    }
    db.instances.push(vm)

    body.disks.forEach((d) => {
      const disk: Json<DiskWithVmId> = {
        id: d.id,
        uuid: uuid(),
        size: d.size,
        storage_id: d.storage_id,
        vm_id: vm.id,
      }
      db.disks.push(disk)
    })

    setTimeout(() => {
      vm.status = 'RUNNING'
      vm.ip_addresses = [{ ip: '192.168.1.200', type: 'IPV4' }]
    }, 3000)

    return json({
      vm: vm,
      job_id: '',
    })
  },
  getAllDataCenters: NotImplemented,
  getVm({ path }) {
    const vm = db.instances.find((instance) => instance.id === path.vmId)
    if (!vm) throw notFoundErr(`vm '${path.vmId}'`)
    return vm
  },
  listFiles: NotImplemented,
  listJobsForObject: NotImplemented,
  login: NotImplemented,
  ping: NotImplemented,
  powerOffVm({ path }) {
    const vm = db.instances.find((i) => i.id === path.vmId)
    if (!vm) throw notFoundErr(`vm '${path.vmId}'`)
    vm.status = 'STOPPING'
    setTimeout(() => {
      vm.status = 'STOPPED'
    }, 3000)
    return json({
      vm_id: vm.id,
      job_id: '',
    })
  },
  powerOnVm({ path }) {
    const vm = db.instances.find((i) => i.id === path.vmId)
    if (!vm) throw notFoundErr(`vm '${path.vmId}'`)
    vm.status = 'STARTING'
    setTimeout(() => {
      vm.status = 'RUNNING'
    }, 3000)
    return json({
      vm_id: vm.id,
      job_id: '',
    })
  },
  queryJob: NotImplemented,
  refreshHost: NotImplemented,
  resetVm({ path }) {
    const vm = db.instances.find((i) => i.id === path.vmId)
    if (!vm) throw notFoundErr(`vm '${path.vmId}'`)
    vm.status = 'REBOOTING'
    setTimeout(() => {
      vm.status = 'RUNNING'
    }, 3000)
    return json({
      vm_id: vm.id,
      job_id: '',
    })
  },
  uploadFile: NotImplemented,
  getHosts({ query }) {
    const hosts = db.hosts
    return paginated(query, hosts)
  },
  GetVms({ query }) {
    const instances = db.instances
    return paginated(query, instances)
  },
  getVmDisks({ path }) {
    const disks = db.disks.filter((disk) => disk.vm_id === path.vmId)
    if (disks.length === 0) throw notFoundErr(`disk '${path.vmId}'`)
    const d = disks.map((d) => d as Json<Disk>)
    return { items: d }
  },
})
